.form-radios .form-radio:disabled:checked + .option:hover::after {
  transform: scale(0.5);
}

.form-radios .option {
  display: flex;
  align-items: flex-start;
  margin-bottom: 0;
  cursor: pointer;
  font-weight: 400;
  line-height: 1.45;
  position: relative;
}

.form-radios .option::before, .form-radios .option::after {
  content: ' ';
}

.form-radios .option::before {
  display: inline-block;
  height: 20px;
  min-width: 20px;
  background: #000;
  border: 1px solid #000;
  border-radius: 50%;
  margin: 2px 8px 0 0;
}

.form-radios .option::after {
  width: 20px;
  height: 20px;
  background: #000;
  border-radius: 100%;
  position: absolute;
  top: 2px;
  left: 0;
  opacity: 0;
  transition: all .5s ease;
  transform-origin: center;
  transform: scale(0);
}

.form-radios .option:hover::after {
  opacity: 1;
  transform: scale(0.5);
}

